@import url('https://fonts.googleapis.com/css?family=Roboto');

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-image: -webkit-radial-gradient(#fe718f 0%, #fe8183 42%, #ff9b73 87%);
  background-image: -o-radial-gradient(#fe718f 0%, #fe8183 42%, #ff9b73 87%);
  background-image: radial-gradient(#fe718f 0%, #fe8183 42%, #ff9b73 87%);
}



